.movie-list {
    width: 45%;
    display: flex;
    margin-bottom: 30px;
}

.movie-info {
    margin-left: 15px;
}

a {
    text-decoration: none;
    color: #fff;
}

.genres {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
}

.genres li {
    background-color: #eee;
    padding: 5px 10px;
    border-radius: 15px;
    margin-right: 10px;
    margin-bottom: 8px;
    color: #333;
}
